.login{
    width: 100vw;
    height: 100vh;
    background-image: url(../../assets/images/login-bgi.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    user-select: none;

    &::before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(0,116,177,0.9);
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        margin: auto;
        z-index: 1;
    }

    &__title{
        position: relative;
        z-index: 33;
        margin-top: 20px;
        font-size: 14px;
        span{
            font-size: 16px;
            color: #fff;
            margin-left: 4px;
            cursor: pointer;
            &:hover{
                text-decoration: underline;
            }
        }
    }

    &__subtitle{
        color: #fff;
        position: absolute;
        z-index: 11;
        bottom: 20px;

    }

    &-wrapper{
        position: relative;
        z-index: 22;
        color: #fff;
        font-size: 65px;
        display: flex;
        align-items: center;
        font-weight: bold;
        font-family: 'Mulish', sans-serif;
        margin-bottom: 25px;
        cursor: pointer;


        img{

            background-color: #fff;
            width: 70px;
            height: 70px;
            border-radius: 10px;
            margin-left: 3px;
        }
    }

    .form{
        position: relative;
        z-index: 44;
        display: flex;
        flex-direction: column;
        align-items: center;

        &__input{
            border: none;
            width: 250px;
            margin-bottom: 20px;
            height: 20px;
            padding: 10px;
            border-radius: 20px;
            outline: none;
            color: #081fce;
            font-size: 15px;

            &::placeholder{
                font-size: 15px;
                color: grey;
            }
        }

        &__btn{
            width: 250px;
            border-radius: 20px;
            padding: 12px;
            font-weight: bold;
            background-color: #fff;
            border: none;
            outline: none;
            color: #0014b1;
            cursor: pointer;
            font-size: 18px;
        }
    }
}