.message-1 {
    padding-bottom: 200px;

    .own-wrapper{
        &-top{
            padding: 10px 0 0 0;
            margin-bottom: 15px;
            &__title{
                color: grey;
                font-size: 14px;
                font-weight: normal;
                position: relative;
                text-align: center;

                &::before {
                    content: '';
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 20px;
                    margin: auto;
                    width: 180px;
                    height: 1px;
                    background-color: #EBEBEB;
                }

                &::after {
                    content: '';
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    right: 20px;
                    margin: auto;
                    width: 180px;
                    height: 1px;
                    background-color: #EBEBEB;
                }
            }
        }
    }

    .message-wrapper {
        display: flex;
        padding: 12px;
        &-left {
            margin-right: 10px;
            display: block;
        }

        &-right {
            &__title{
                font-size: 14px;
                font-weight: bolder;
                margin-bottom: 20px;

                span {
                    margin-left: 8px;
                    font-weight: normal;
                    font-size: 12px;
                }
            }
        }

        .text-wrapper {
            &__title {
                font-size: 14px;
                
            }
        }
    }
}