.product{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
    &-top{
        border: 1px solid rgba(0,0,0,0.2);
        padding: 4px;
        transition: all ease 0.5s;
        cursor: pointer;
        margin-bottom: 4px;
        border-radius: 5px;
        
        &:first-child{
            padding-bottom: 0;
            padding-top: 4px;
        }
        &__img{
            width: 30px;
            height: 30px;
            object-fit: cover;
        }

        &:hover{
            box-shadow: 0 0 6px 3px rgba(105,105,105,0.3);
        }
    }

    &__title{
        font-size: 13px;
        color: grey;
    }
}