.user-card {
    border: 1px solid rgba(0,0,0,0.1);
    border-radius: 15px;
    overflow: hidden;
    width: 180px;
    padding-bottom: 10px;
    margin-bottom: 20px;
    // height: 250px;
    position: relative;


    &:hover{
        box-shadow: 0 2px 5px 2px rgba(0,0,0,0.1);
    }

    &-top{
        width: 100%;
        height: 55px;
        background-repeat: no-repeat;
        background-image: url(../../assets/images/user-bgi.jpg);
        background-position: center;
        object-fit: contain;
    }

    &__img{
        width: 60px;
        height: 60px;
        border-radius: 50%;
        margin: 0 auto;
        display: block;
        margin-top: -30px;
        margin-bottom: 10px;
    }

    &__title{
        font-size: 16px;
        width: 150px;
        margin: 0 auto;
        text-align: center;
        cursor: pointer;

        &:hover {
            text-decoration: underline;
        }
    }

    &__subtitle{
        font-size: 14px;
        text-align: center;
        margin: 0 auto;
        margin-top: 7px;
    }

    &__connected {
        font-size: 10px;
        color: grey;
        margin: 0 auto;
        margin-top: 10px;
        text-align: center;
        margin-bottom: 10px;
        position: relative;
        // bottom: 10px;

        &::before {
            content: '';
            width: 15px;
            height: 15px;
            background-repeat: no-repeat;
            position: absolute;
            top: 0;
            bottom: 0;
            margin: auto;
            left: 19px;
            background-image: url(../../assets/images/icons/connect.svg);
            background-size: contain;
        }
    }

    &__btn {
        display: block;
        margin: 0 auto;
        cursor: pointer;
        color: #0a66c2;
        background-color: transparent;
        border: 1px solid #0a66c2;
        padding: 5px 20px;
        border-radius: 18px;
    }
}