.overlay {
    width: 0;
    height: 0;
    overflow: hidden;

    &.visible {
        width: 100vw;
        height: 100vh;
        background-color: rgba(0,0,0,0.3);
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        z-index: 100;
    }
}