.course {
    
    &-top{
        background-color: #fff;
        box-shadow: 0 0 14px 2px grey;
        border-radius: 10px;
        padding: 12px 0;
        box-sizing: border-box;
        margin-bottom: 20px;
        
        &-wrapper{
            padding: 0 12px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 8px;

            &__img{
                width: 15px;
                height: 15px;
                object-fit: contain;
                cursor: pointer;
                border-radius: 50%;
                padding: 5px;
                transition: all ease 0.5s;

                &:hover{
                    background-color: #EBEBEB;
                }
            }

            &__title{
                font-size: 14px;
            }
        }

        &__link{
            padding-left: 12px;
            font-size: 12px;
            color: grey;
            display: flex;
            align-items: center;
            transition: all ease 0.5s;
            
            &:hover{
                background-color: #EBEBEB;
            }

            svg{
                // margin-top: 2px;
                margin-left: 3px;
            }
        }

        &-article{
            margin-bottom: 8px;
            transition: all ease 0.5s;
            cursor: pointer;
            padding: 4px 12px;
            overflow: hidden;

            &:hover{
                background-color: #EBEBEB;
            }
            &__title{
                font-size: 13px;
                margin-bottom: 4px;
            }

            &__text{
                font-size: 10px;
                color: grey;
            }
        }
    }

    &-bottom{

        &.has-changed {
            position: fixed;
            top: 8%;
            width: 270px;
        }
        
        &-wrapper{
            box-shadow: 0 0 14px 2px grey;
            overflow: hidden;
            border-radius: 10px;
            background-color: #fff;
            padding: 0 8px;
            box-sizing: border-box;
            margin: 0;
            margin-bottom: 20px;

            &__img {
                width: 254px;
                object-fit: cover;
                height: 250px;
                margin: 0 !important;            
            }
        }

        &-links {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
            margin: 0 auto;

            a {
                font-size: 14px;
                color: grey;
                margin-right: 12px;
                line-height: 1.7;
                display: block;
                &:hover{
                    text-decoration: underline;
                }
            }
        }

        .info {
            margin-top: 20px;
            display: flex;
            flex-direction: column;
            align-items: center;
            svg {
                fill: #0074b1;
            }

            a {
                display: block;
            }

            span {
                font-size: 12px;
                color: grey;
            }
        }
    }
}