.header{
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 201;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    user-select: none;
    &-inner{
        display: flex;
        justify-content: space-between;
        align-items: center;
        &-left{
            display: flex;
            align-items: center;
            box-sizing: border-box;
            a{
                margin-right: 15px;
                display: inline-block;
            }
            &__img{
                width: 35px;
                height: 35px;
                object-fit: contain;
            }

            .input-wrapper{
                background-color: #EEF3F8;
                padding: 5px;
                display: flex;
                align-items: center;
                padding-left: 10px;
                box-sizing: border-box;

                &__img{
                    width: 25px;
                    height: 25px;
                }
                
                &__input{
                    width: 200px;
                    border: none;
                    background-color: transparent;
                    outline: none;
                    padding: 7px;

                    &::placeholder{
                        color: grey;
                    }
                }
            }
        }

        &-right{
            display: flex;

            &-link{
                color: grey;
                transition: all ease 0.5s;
                padding: 5px 10px;
                cursor: pointer;

                &:nth-child(6) {
                    border-right: 0.4px solid gray;
                    padding-right: 19px;
                    position: relative;
                }

                &:nth-child(7) {
                    padding-right: 15px;
                }

                &.selected{
                    color: #000 !important;
                    border-bottom: 2px solid #000;
                }
            }

            .view-profile{
                position: absolute;
                background-color: #fff;
                border-radius: 10px;
                left: -76px;
                margin: auto;
                top: 58px;
                height: 0;
                overflow: hidden;
                transition: ease-in-out;
                
                &.opened {
                    box-shadow: 0 0 14px 2px grey;
                    height: auto;
                }

                &-top{
                    display: flex;
                    align-items: center;
                    margin-bottom: 10px;
                    padding: 12px;
                    padding-bottom: 0;

                    &__img{
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                        margin-right: 10px;
                        margin-bottom: 5px;
                        object-fit: cover;
                    }

                    svg{
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                        margin-right: 10px;
                        margin-bottom: 5px;  
                    }

                    &__title{
                        font-size: 18px;
                        text-align: center;
                    }

                    &__subtitle{
                        font-size: 12px;
                        text-align: center;
                    }
                }

                &__link{
                    display: block;
                    border: 1px solid #0074b1;
                    padding: 2px;
                    text-align: center;
                    box-sizing: border-box;
                    border-radius: 15px;
                    color: #0074b1;
                    font-size: 14px;
                    margin: 0 12px;
                    margin-bottom: 10px;
                }

                &-account{
                    padding: 12px;
                    padding-top: 5px;
                    border-top: 1px solid rgba(0,0,0,0.2);

                    &__title{
                        color: #000;
                        margin-bottom: 5px;
                        font-size: 16px;
                    }

                    
                    &__link{
                        color: #000;
                        font-size: 12px;
                        display: block;
                        color: grey;
                        margin-bottom: 4px;

                        &:hover{
                            text-decoration: underline;
                        }
                        
                        &.sign-out{
                            margin-bottom: 0;
                            font-size: 14px;
                        }

                        &.premium-box{
                            padding-left: 25px;
                            position: relative;

                            &::before{
                                content: '';
                                width: 18px;
                                height: 18px;
                                position: absolute;
                                left: 0;
                                top: 0;
                                bottom: 0;
                                margin: auto;
                                background-image: url(../../assets/images/icons/box.svg);
                                background-repeat: no-repeat;
                            }
                        }
                    }
                }
            }
        }
    }

    .profile-holder{
        &__img{
            width: 22px;
            height: 22px;
            border-radius: 50%;
            object-fit: cover;
        }

        &__title{
            position: relative;
            padding-right: 5px;
            font-size: 12px;
            margin-top: -3px;
            font-weight: normal;

            &::after{
                content: '';
                position: absolute;
                width: 10px;
                height: 10px;
                background-repeat: no-repeat;
                background-size: contain;
                background-image: url(../../assets/images/icons/triangle.svg);
                right: -10px;
                top: -2px;
                bottom: -3px;
                margin: auto;
            }
        }
    }
}