.message {
    display: flex;
    justify-content: space-between;
    padding-top: 50px;
    overflow-y: hidden;
    height: 88vh;
    position: sticky;
    bottom: 0;
    &-left{
        display: flex;
        width: 65%;
        background-color: #fff;
        box-shadow: 0 0 9px 1px rgb(0 0 0 / 20%);
        border-radius: 10px;
        .user-block {
            border-right: 1px solid rgba(0,0,0,0.2);
            height: 100%;
            width: 40%;
            &-top {
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-bottom: 1px solid rgba(0,0,0,0.2);
                padding: 8px;
                box-sizing: border-box;

                div {
                    color: grey;
                    svg {
                        padding: 5px;
                        border-radius: 50%;
                        cursor: pointer;
                        &:hover {
                            background-color: #EBEBEB;
                        }

                        &:last-child {
                            height: 20px;
                        }
                    }
                }
            }

            &-search {
                padding: 8px;
                display: flex;
                justify-content: center;
                border-bottom: 1px solid rgba(0,0,0,0.2);
                box-sizing: border-box;

                .user-div {
                    background-color: #EEF3F8;
                    width: 99%;
                    position: relative;
                    padding: 4px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    border: 1px solid transparent;

                    &:hover {
                        border: 1px solid rgba(0,0,0,0.2);
                    } 

                    &__img {
                        padding-left: 5px;
                        width: 15px;
                        height: 15px;
                        object-fit: cover;

                        &:last-child {
                            padding-left: 0;
                            padding: 5px;
                            border-radius: 50%;
                            cursor: pointer;
                            &:hover {
                                background-color: #EBEBEB;
                            }
                        }
                    }
                    

                    
                    input {
                        background-color: transparent;
                        border: none;
                        outline: none;
                        width: 80%;
                        display: block;
                        height: 100%;
                    }
                }
            }


        }
    }

    &-block {
        height: 100%;
        width: 60%;
        position: relative;
        overflow-y: scroll;

        .texting-area {
            position: fixed;
            bottom: 0;
            width: 25.7%;

            @media only screen and(max-width:1600px) {
                width: 31.2%;
            }

            @media only screen and(max-width:1400px) {
                width: 34.7%;
            }
            
            @media only screen and(max-width:1399px) {
                width: 34.7%;
            }


            &-top {
                width: 100%;
                button[type=submit] {
                    display: none;
                }
                &__text {
                    outline: none;
                    border: none;
                    border-top: 1px solid rgba(0,0,0,0.2);
                    border-bottom: 1px solid rgba(0,0,0,0.2);
                    display: block;
                    width: 100%;
                    height: 80px !important;
                    resize: none;
                    padding: 5px 10px;
                    box-sizing: border-box;


                    &:focus {
                        border-top: 1px solid #2A6249;
                    }
                }
            }

            &-bottom {
                display: flex;
                justify-content: space-between;
                align-self: center;
                padding: 12px 12px 40px 12px;
                background-color: #fff;

                &-left {
                    display: flex;

                    input[type=file] {
                        display: none;
                    }

                    &__label {
                        margin-right: 10px;
                        cursor: pointer;
                    }

                    &__img {
                        width: 30px;
                        height: 25px;
                        object-fit: contain;
                    }
                }

                &-right {
                    display: flex;
                    align-self: center;

                    &__btn {
                        margin-right: 10px;
                        padding: 5px 12px;
                        border-radius: 10px;
                        font-size: 14px;
                        color: grey;
                        background-color: #EBEBEB;
                        border: none;
                        outline: none;
                        cursor: pointer;
                    }
                    svg {
                        color: grey;
                        padding: 5px;
                        box-sizing: border-box;
                        border-radius: 50%;
                        cursor: pointer;
                        
                        &:hover {
                            background-color: #EBEBEB;
                        }
                    }
                }
            }
        }
    }
}