.connection {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 9px 12px;
    padding-right: 17px;
    transition: all ease 0.3s;

    &:last-child{
        margin-bottom: 0;
    }

    &:hover{
        background-color: #EBEBEB;
    }

    &-left{
        display: flex;
        align-items: center;
        color: grey;

        &__title{
            font-weight: normal;
            margin-left: 10px;
            font-size: 16px;
        }
    }

    &__title{
        color: grey;
        font-size: 14px;
    }
}