.user-message {
    display: block;
    padding: 12px 5px 12px 12px;
    border-left: 4px solid transparent;
    position: relative;

    &:hover {
        background-color: rgba(0,0,0,0.1);
    }

    &::after {
        content: '';
        width: 150px;
        position: absolute;
        right: 0;
        bottom: 0;
        height: 1px;
        background-color: rgba(0,0,0,0.2);
    }

    &.selected-user-message {
        background-color: #eef3f8;
        border-left: 4px solid #057642;
    }

    .user-div {
        width: 150px;
    }
    
    &-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &__name {
            display: block;
            color: #000;
            font-size: 16px;
            font-weight: normal;
            margin-bottom: 4px;
            margin-left: -10px;            
        }
        
        &__email {
            display: block;
            font-size: 13px;
            color: grey;
            font-weight: normal;
            margin-left: -10px;
        }
        
        .user-message__img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
        }
    }

    span {
        align-self: flex-start;
        font-size: 12px;
        color: grey;
    }
}