.network-sidebar {
    box-shadow: 0 0 24px 1px rgba(0, 0, 0, 0.2);
    border-radius: 15px;
    padding: 12px 0;
    background-color: #fff;
    margin-bottom: 30px;

    &.netFix {
        position: sticky;
        top: 8%;
        width: 335px;
    }

    .hide-section.unvisible {
        height: 0;
        overflow: hidden;
    }

    &__title {
        font-size: 16px;
        margin-bottom: 7px;
        padding: 0 12px;
    }

    .show-more {
        margin-left: 12px;
        font-size: 16px;
        color: grey;
        margin-top: 5px;
        position: relative;
        display: inline-block;
        padding: 5px 40px 5px 5px;
        cursor: pointer;
        transition: all ease 0.3s;
        margin-bottom: 10px;

        &.bgc {
            background-color: #EBEBEB;

            &::after {
                transform: rotate(180deg);
            }
        }

        &:hover {
            background-color: #EBEBEB;
        }

        &::after {
            content: '';
            position: absolute;
            right: 5px;
            bottom: 0;
            top: 3px;
            margin: auto;
            width: 20px;
            height: 20px;
            background-size: contain;
            background-repeat: no-repeat;
            background-image: url(../../assets/images/icons/upwards-arrow.svg);
        }
    }

    .img-wrapper {
        padding: 10px 5px;
        box-sizing: border-box;
        border-top: 1px solid rgba(0, 0, 0, 0.2);
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);

        &__img {
            margin: 0 auto;
            display: block;
        }
    }

    .personal-contacts {
        padding: 12px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        padding-bottom: 18px;

        &__title {
            padding: 0 12px;
            color: grey;
            font-size: 16px;
            text-align: center;
            margin-bottom: 8px;
        }

        &__text {
            font-size: 12px;
            color: grey;
            text-align: center;
            margin-bottom: 10px;

            a {
                color: #0a66c2;

                &:hover {
                    text-decoration: underline;
                }
            }
        }

        &__input {
            width: 90%;
            margin: 0 auto;
            display: block;
            padding: 5px;
            box-sizing: border-box;
            outline: none;
            margin-bottom: 12px;
        }

        &__btn {
            width: 30%;
            display: block;
            margin: 0 auto;
            padding: 6px 12px;
            font-size: 15px;
            color: #0a66c2;
            border: 1px solid #0a66c2;
            border-radius: 16px;
            outline: none;
            background-color: #fff;
            cursor: pointer;
            margin-bottom: 12px;
        }

        &__link {
            color: grey;
            font-size: 14px;
            text-align: center;
            display: block;

            &:hover {
                text-decoration: underline;
                color: #0a66c2;
            }
        }
    }

    .network-bottom {
        display: flex;
        flex-wrap: wrap;
        padding: 10px 14px;
        box-sizing: border-box;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        width: 332px;

        a {
            font-size: 13px;
            color: grey;
            margin-right: 12px;
            line-height: 1.7;
            display: block;

            &:hover {
                text-decoration: underline;
                color: #0a66c2;
            }
        }
    }

    .info {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        svg {
            fill: #0074b1;
        }

        a {
            display: block;
        }

        span {
            font-size: 12px;
            color: grey;
        }
    }
}