.network-users {
    margin-bottom: 30px;

    &-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #fff;
        padding: 14px;
        box-sizing: border-box;
        border-radius: 10px;
        box-shadow: 0 0 9px 1px rgba(0, 0, 0, 0.2);
        margin-bottom: 20px;

        &__title {
            font-size: 15px;
            font-weight: normal;
        }

        &__subtitle {
            color: grey;
            font-size: 14px;
            padding: 6px;
            box-sizing: border-box;
            border-radius: 5px;

            &:hover {
                background-color: #EBEBEB;
            }
        }
    }

    &-bottom {
        background-color: #fff;
        padding: 18px 14px;
        padding-bottom: 0;
        border-radius: 10px;
        box-shadow: 0 0 9px 1px rgba(0, 0, 0, 0.2);

        &-wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;

            &__title {
                font-size: 15px;
                font-weight: normal;
            }

            &__subtitle {
                color: grey;
                font-size: 14px;
                padding: 6px;
                box-sizing: border-box;
                border-radius: 5px;
                cursor: pointer;

                &:hover {
                    background-color: #EBEBEB;
                }
            }
        }
    }

    .users-wrapper {
        &-holder {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-wrap: wrap;
        }
    }

    .loader-wrapper{
        width: 100vw;
        height: 100vh;
        background-color: #fff;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 121;
        overflow: hidden;
        .loader {
            border: 16px solid #f3f3f3;
            border-top: 16px solid #3498db;
            border-radius: 50%;
            width: 120px;
            height: 120px;
            animation: spin 2s linear infinite;
        }
    
        @keyframes spin {
            0% {
                transform: rotate(0deg);
            }
    
            100% {
                transform: rotate(360deg);
            }
        }
    }
}