.profile{
    border-radius: 10px;
    background-color: #fff;
    overflow: hidden;
    box-shadow: 0 0 14px 2px grey;

    &-top{
        width: 100%;
        height: 100px;
        background-image: url(../../assets/images/bgc.jfif);
        background-repeat: no-repeat;
        background-size: cover;
    }

    &-center{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(0,0,0,0.2);
        &__img{
            width: 80px;
            height: 80px;
            border-radius: 50%;
            margin-top: -30px;
            object-fit: cover;
            margin-bottom: 10px;
        }

        svg{
            width: 80px;
            height: 80px;
            border-radius: 50%;
            margin-top: -30px;
            object-fit: cover;
            margin-bottom: 10px;
            background-color: #fff;
        }

        &__title{
            font-size: 20px;
            color: #000;
            margin-bottom: 5px;
            transition: all ease 0.5s;
            &:hover{
               text-decoration: underline;
            }
        }

        &__job{
            font-size: 12px;
            font-weight: normal;
            color: grey;
        }
    }

    &-connections{
        margin: 10px 0;
        transition: all ease 0.5s;
        display: block;
        &:hover{
            background-color: #EBEBEB;
        }
        &-wrapper{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 5px;
            padding: 4px 15px;
            padding-bottom: 0;

            &__title{
                color: grey;
                font-size: 16px;
            }
        }

        &__title{
            padding-left: 15px !important;
            font-size: 13px;
            color: #000;
            padding-bottom: 4px;
        }
    }

    &-premium{
        display: block;
        padding: 12px;
        border-top: 1px solid rgba(0,0,0,0.2);
        border-bottom: 1px solid rgba(0,0,0,0.2);
        transition: all ease 0.5s;

        &__title{
            color: grey;
            font-size: 14px;
            margin-bottom: 5px;
        }

        &:hover{
            background-color: #EBEBEB;
        }

        &__subtitle{
            color: #000;
            font-size: 12px;
            padding-left: 25px;
            position: relative;

            &::before{
                content: '';
                width: 18px;
                height: 18px;
                position: absolute;
                left: 0;
                top: 0;
                bottom: 0;
                margin: auto;
                background-image: url(../../assets/images/icons/package.svg);
                background-repeat: no-repeat;
            }
        }
    }

    &-saved{
        display: flex;
        padding: 12px;
        align-items: center;
        color: grey;
        transition: all ease 0.5s;

        svg{
            margin-right: 5px;
            fill: grey !important;
            path {
                fill: grey !important;
            }
        }

        &:hover{
            background-color: #EBEBEB;
        }

        &__title{
            color: #000;
        }
    }
}