.events {
    display: flex;
    align-items: center;
    padding: 10px;
    box-sizing: border-box;
    transition: all ease 0.5s;
    border-radius: 5px;
    margin-right: 30px;

    &:hover{
        background-color: #EBEBEB;
        cursor: pointer;
    }

    &__img{
        width: 25px;
        height: 25px;
        object-fit: contain;
        margin-right: 8px;
    }

    &__title{
        color: grey;
        font-size: 14px;
        font-weight: normal;
    }
}