.all-users {
    width: 0;
    height: 0;
    overflow: hidden;
    
    &.allsee{
        position: fixed;
    top: 10%;
    left: 0;
    right: 0;
    margin: auto;
    width: 650px;
    height: 550px;
    background-color: #fff;
    z-index: 232;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0 0 14px 2px grey;
    overflow-y: scroll;
    }

    ::-webkit-scrollbar {
        width: 8px;
        border-radius: 10px;
    }
      ::-webkit-scrollbar-track {
        background: #f1f1f1;
    }
      
      ::-webkit-scrollbar-thumb {
        background: #888;
    }
      
      ::-webkit-scrollbar-thumb:hover {
        background: #555;
    }

    &-top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 15px 12px;
        border-bottom: 1px solid rgba(0,0,0,0.2);
        position: sticky;
        top: 0;
        background-color: #fff;
        z-index: 290;

        &__title{
            font-size: 16px;
            font-weight: normal;
        }

        svg {
            color: grey;
            padding: 5px;
            border-radius: 50%;
            cursor: pointer;
            transition: all ease 0.3s;

            &:hover{
                background-color: #EBEBEB;
            }
        }
    }

    .loader-wrapper{
        width: 100vw;
        height: 100vh;
        background-color: #fff;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 121;
        overflow: hidden;
        .loader {
            border: 16px solid #f3f3f3;
            border-top: 16px solid #3498db;
            border-radius: 50%;
            width: 120px;
            height: 120px;
            animation: spin 2s linear infinite;
        }
    
        @keyframes spin {
            0% {
                transform: rotate(0deg);
            }
    
            100% {
                transform: rotate(360deg);
            }
        }
    }

    &-bottom {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 20px;
    }
}