body{
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    background-color: #F3F2EF;
    user-select: none;
}

h1,h2,h3,h4,h5,h6,p,a {
    margin: 0;
    padding: 0;
    text-decoration: none;
}

.container {
    width: 100%;
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
}

.app {
    &.app-hidden {
        width: 100vw;
        height: 100vh;
        overflow: hidden;
    }

    &.hidden-app{
        width: 100vw;
        height: 100vh;
        overflow: hidden;
    }
}