.message-card {
    margin-top: 20px;
    margin-left: -50px;
    display: flex;

    &__img {
        margin-right: 10px;
    }

    &-wrapper {
        &__name {
            font-size: 16px;
            display: flex;
            align-items: center;
            span {
                font-size: 13px;
                margin-left: 5px;
                color: grey;
            }
        }

        &__text {
            font-size: 14px;
            color: #000;
            width: 300px;
            line-height: 20px;
        }
    }
    
}