.work{
    background-color: #fff;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    box-shadow: 0 0 14px 2px grey;
    box-sizing: border-box;
    width: 0;
    height: 0;
    overflow: hidden;
    position: absolute;
    z-index: 101;
    // right: -350px;
    // transition: all ease 0.1s;
    
    
    &.show {
        width: 100%;
        max-width: 350px;
        // transform: none;
        height: 93.8vh;
        right: 0;
        bottom: 0;
        margin: auto;
        padding: 20px;
        display: flex;
        align-items: center;
        flex-direction: column;
    }

    &-top{
        display: flex;
        width: 100%;
        justify-content: flex-end;
        color: grey;
        margin-bottom: 20px;

        svg {
            padding: 5px;
            transition: all ease 0.5s;
            border-radius: 50%;
            cursor: pointer;

            &:hover{
                background-color: #EBEBEB;
            }
        }
    }

    &-center{
        border: 1px solid rgba(0,0,0,0.2);
        margin-bottom: 20px;
        border-radius: 10px;

        .work-link{
            div{
                margin-right: 0;
            }
        }

        .work-learn{
            div{
                img{
                    margin-bottom: -8px;
                }
            }
        }

        a{
            display: block;
        }

        &__title{
            padding: 12px;
            border-bottom: 1px solid rgba(0,0,0,0.2);
        }

        &-wrapper{
            padding: 12px 0;
            &-top{
                display: flex;
                margin-bottom: 10px;
                padding: 0 12px;
            }

            &-bottom{
                display: flex;
                padding-left: 12px;
            }
        }
    }

    &-bottom{
        border: 1px solid rgba(0,0,0,0.2);
        border-radius: 10px;
        width: 289px;
        // margin-bottom: 50px;

        &__title{
            padding: 12px;
            border-bottom: 1px solid rgba(0,0,0,0.2);
        }

        &-center{
            padding: 12px;
            border-bottom: 1px solid rgba(0,0,0,0.2);

            a {
                margin-bottom: 8px;
                display: block;
                color: #000;
                &:hover{
                    text-decoration: underline;
                }

                &:last-child{
                    margin-bottom: 0;
                }
            }

            &__title{
                color: #000;
                font-size: 14px;
            }

            &__subtitle{
                color: grey;
                font-size: 12px;
            }
        }

        &-add{
            padding: 8px 12px;
            display: flex;
            align-items: center;
            a {
                margin-right: 5px;
                display: block;
                color: #000;
                font-size: 14px;
                &:hover{
                    text-decoration: underline;
                }
            }
        }
    }
}