.post {
    background-color: #fff;
    box-shadow: 0 0 24px 1px grey;
    border-radius: 15px;
    padding: 12px;
    margin-bottom: 25px;

    &-bottom{
        display: flex;
        align-items: center;

        .events__img{
            margin-right: 12px;
        }
    }

    &__title{
        font-size: 16px;
        margin-bottom: 10px;
    }

    &-top{
        display: flex;
        align-items: center;
        margin-bottom: 15px;

        &__img{
            width: 40px;
            height: 40px;
            object-fit: cover;
            margin-right: 20px;
            border-radius: 50%;
        }

        &-right{
            &__title{
                font-size: 14px;
                color: #000;
                font-weight: bolder;

                &:hover{
                    text-decoration: underline;
                }
            }

            &__subtitle{
                font-size: 12px;
                color: grey;
            }
        }

    }
}