.header-option{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &__img{
        width: 20px;
        height: 20px;
        border-radius: 50%;
        object-fit: cover;
    }

    &__title{
        font-size: 12px;
        font-weight: normal;

        &.triangle{
            position: relative;
            padding-right: 5px;

            &::after{
                content: '';
                position: absolute;
                width: 10px;
                height: 10px;
                background-repeat: no-repeat;
                background-size: contain;
                background-image: url(../../assets/images/icons/triangle.svg);
                right: -10px;
                top: -2px;
                bottom: -3px;
                margin: auto;
            }
        }
    }
    &:hover{
        color: #000;
    }
}