.feed {
    &-top{
        box-shadow: 0 0 24px 1px grey;
        border-radius: 15px;
        padding: 12px;
        background-color: #fff;
        margin-bottom: 30px;
        
        &-input-wrapper{
            margin-bottom: 10px;
            display: flex;
            align-items: center;
            &__img{
                width: 50px;
                height: 50px;
                object-fit: cover;
                border-radius: 50%;
                cursor: pointer;
            }

            svg {
                width: 50px;
                height: 50px;
                object-fit: cover;
                border-radius: 50%;
                cursor: pointer;
            }

            form{
                width: 100%;
                display: block;
            }

            &__input{
                border: 1px solid grey;
                border-radius: 35px;
                padding: 16px;
                width: 95%;
                box-sizing: border-box;
                outline: none;
                margin-left: 15px;

                &::placeholder{
                    color: grey;
                }
            }

            &__btn{
                display: none;
            }
        }

        &-wrapper{
            display: flex;
            align-items: center;
        }
    }
}