.options{
    background-color: #fff;
    // width: 30%;
    border-radius: 10px;
    box-shadow: 0 0 14px 2px grey;
    overflow: hidden;
    transition: all ease 0.5s;

    &.changed {
        position: fixed;
        top: 8%;
        width: 265px;
    }

    &-top{
        padding: 12px;
        border-bottom: 1px solid rgba(0,0,0,0.2);

        &__link{
            margin-bottom: 8px;
            display: inline-block;
            &:last-child{
                margin-bottom: 0;
            }
            &:hover{
                text-decoration: underline;
            }
        }

        &-wrapper{
            display: flex;
            align-items: center;
            justify-content: space-between;

            svg{
                color: grey;
                cursor: pointer;
                padding: 5px;
                transition: all ease 0.5s;
                border-radius: 50%;

                &:hover{
                    background-color: #EBEBEB;
                }
            }
        }
    }

    &-bottom{
        padding: 12px;
        text-align: center;
        display: block;
        color: grey;
        transition: all ease 0.5s;

        &:hover{
            background-color: #EBEBEB;
        }
    }
}